import React, { memo } from 'react';
import classNames from 'classnames';
import './ProductRoadmap.scss';
import { ProductRoadmapProps, ProductRoadmapItem } from './ProductRoadmap.types';

// Utility to clamp numeric props
const clamp = (val: number | undefined, min: number, max: number, def: number): number => {
  if (val == null || Number.isNaN(val)) return def;
  return Math.min(max, Math.max(min, val));
};

const RoadmapItem: React.FC<{ item: ProductRoadmapItem; columnWidth: number; maxLines: number }>= ({ item, maxLines }) => {
  const {
    content,
    dateOffset = 0,
    length = 1,
    partialLength = 1,
    verticalPosition = 1,
    ariaLabel,
  } = item;

  return (
    <div
      className="nhsuk-product-roadmap__item"
      style={{
        ['--item-vertical-position' as any]: verticalPosition,
        ['--item-length' as any]: length,
        ['--item-partial-length' as any]: partialLength,
        ['--date-offset' as any]: dateOffset,
      }}
      role="listitem"
      aria-label={ariaLabel}
    >
      <div className="nhsuk-product-roadmap__item-bg" aria-hidden="true" />
      <div className="nhsuk-product-roadmap__item-content" style={{ ['--item-height' as any]: maxLines }}>
        {content}
      </div>
    </div>
  );
};

export const ProductRoadmap: React.FC<ProductRoadmapProps> = memo(
  ({
    dateHeadings,
    categories,
    columnWidthPx = 400,
    itemMaxLines = 2,
    className,
    ariaLabel = 'Product roadmap',
    ...rest
  }) => {
    const rootCls = classNames('nhsuk-product-roadmap', className);
    const colWidth = clamp(columnWidthPx, 120, 1200, 400);
    const maxLines = clamp(itemMaxLines, 1, 6, 2);

    return (
      <div className={rootCls} style={{ ['--column-width' as any]: `${colWidth}px` }} {...rest}>
        <table className="nhsuk-product-roadmap__table" aria-label={ariaLabel} role="grid">
          <thead>
            <tr className="nhsuk-product-roadmap__row nhsuk-product-roadmap__row--dates">
              <th scope="col"><span className="nhsuk-visually-hidden">Category</span></th>
              {dateHeadings.map((heading, i) => (
                <th key={i} scope="col">{heading}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {categories.map((cat, ci) => (
              <tr key={ci} className="nhsuk-product-roadmap__row" role="row">
                <th scope="row" className="nhsuk-product-roadmap__category-cell">{cat.heading}</th>
                {dateHeadings.map((_, colIndex) => {
                  const columnNumber = colIndex + 1; // 1-based
                  const items = cat.roadmapItems.filter(it => it.date === columnNumber);
                  return (
                    <td key={colIndex} className="nhsuk-product-roadmap__date-cell">
                      <div className="nhsuk-product-roadmap__cell-container" role="list">
                        {items.map((it, idx) => (
                          <RoadmapItem key={idx} item={it} columnWidth={colWidth} maxLines={maxLines} />
                        ))}
                      </div>
                    </td>
                  );
                })}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  }
);

ProductRoadmap.displayName = 'ProductRoadmap';
